<template>
  <div id="cs-pra">
    <head-top title="随堂练习"></head-top>
    <div class="main">
      <div class="content">
        <div class="tt">
          {{currentIndex + '.【' + getPracticeTypeName(practiceInfo['PSType']) + '】' + (practiceInfo['PSScore']||0) + '分'}}
        </div>

        <div class="ct">
          {{practiceInfo['PSContent']}}
        </div>
        <div v-if="[1, 2].indexOf(practiceInfo['PSType']) > -1 " class="ans">
          <div class="an" v-for="(item, k) in practiceOptions" :key="k">
            <span>{{ item['SOSerial']}}：</span>
            <span>{{item['SOContent']}}</span>
          </div>
        </div>
      </div>

      <do-answer route-type="history" :question-type="practiceInfo['PSType']" :question-option="practiceInfo['PSOption']"
        :ableReply="ableReply" :answer-value="practiceAnswer" :module-picture="modulePicture"
        @change="answerChange">
      </do-answer>

      <div class="footer">
        <button v-show="currentIndex> 1" @click="submitAnswer(-1)">上一题</button>
        <button v-if="currentIndex< total" @click="submitAnswer(1)">下一题</button>
        <button v-else-if="ableReply" @click="submitAnswer(1)">提交答案</button>
      </div>
    </div>
  </div>
</template>

<script>
import DoAnswer from "@/components/Question/DoAnswer";
import MyAnswer from "@/components/Question/MyAnswer";
import Survey from "@/components/Question/Survey";

import { practiceTypeName, ModulePicture, PracticeFunc } from "@/utils/enum";
export default {
  name: "",
  components: { DoAnswer, MyAnswer, Survey },
  props: {},
  data() {
    return {
      taskId: this.$route.params.taskId,
      practiceId: this.$route.params.practiceId,
      libraryId: this.$route.params.libraryId,
      classHourId: this.$route.params.classHourId,

      operateType: PracticeFunc.start, //操作步骤 1开始答题 2获取提问和答题 3下发答案和解析 4调研
      practiceInfo: {},
      practiceOptions: [],
      practiceReply: {},
      total: parseInt(this.$route.params.total || "1"),
      practiceAnswer: "",
      modulePicture: ModulePicture.PractiseAnswer,
      ableReply: false,
      currentIndex: 1
    };
  },
  created() {
    this.getPracticeInfo();
  },
  methods: {
    getPracticeInfo() {
      this.$mainHttp.classPractise
        .getPracticeMental({ plID: this.libraryId + "" })
        .then(res => {
          if (res.Result) {
            this.total = res.Total;
            this.getDataInfo();
          } else {
          }
        })
        .catch(err => {});
    },
    getDataInfo() {
      Object.keys(this.practiceInfo).forEach(item => {
        this.$delete(this.practiceInfo, item);
      });
      Object.keys(this.practiceReply).forEach(item => {
        this.$delete(this.practiceReply, item);
      });
      this.practiceOptions = [];
      this.$mainHttp.classPractise
        .getData({
          studentID: this.$store.getters.studentID,
          cipID: this.taskId,
          // cpid: this.practiceId,
          plid: this.libraryId,
          psSerial: this.currentIndex
        })
        .then(res => {
          if (res.Result) {
            this.practiceInfo = res.PSData;
            this.practiceReply = res.PAData ? res.PAData : {};
            this.practiceAnswer = this.practiceReply["PAContent"];

            this.ableReply = this.practiceInfo ? true : false;

            this.practiceOptions = [];
            if (res.SOData && res.SOData.length > 0) {
              res.SOData.forEach(item => {
                let index = this.practiceOptions.findIndex(
                  r => JSON.stringify(r) === JSON.stringify(item)
                );
                if (index === -1) this.practiceOptions.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getPracticeTypeName(val) {
      return practiceTypeName(val);
    },
    answerChange(answer) {
      this.practiceAnswer = answer;
    },
    submitAnswer(val) {
      if (!this.ableReply) {
        if (this.currentIndex >= this.total) {
          this.$router.push("/space/homework");
        }
        this.currentIndex += val;
        this.practiceAnswer = "";
        this.getDataInfo();
        return;
      }
      if (!this.practiceAnswer) {
        this.$toast("请先作答");
      } else {
        let reqData = {
          cipID: this.taskId,
          // cpid: this.practiceId,
          paid: this.practiceReply["PAID"] || "",
          // courseID: this.courseId,
          psID: this.practiceInfo["PSID"],
          studentID: this.$store.getters.studentID,
          paContent: this.practiceAnswer
        };
        this.$mainHttp.classPractise
          .postDataAnswer(reqData)
          .then(res => {
            if (res.Result) {
              if (this.currentIndex === this.total && val > 0) {
                this.submitPractice();
              } else {
                this.currentIndex += val;
                this.practiceAnswer = "";
                if (this.currentIndex > this.total)
                  this.$router.push("/space/homework");
                this.getDataInfo();
              }
            } else if (res.Info) {
              this.$toast(res.Info);
            }
          })
          .catch(err => {
            this.$toast("提交失败，请重试");
          });
      }
    },
    submitPractice() {
      let reqData = {
        cipID: this.taskId,
        studentID: this.$store.getters.studentID
      };

      this.$mainHttp.classPractise
        .postDataPractise(reqData)
        .then(res => {
          if (res.Result) {
            this.$toast.success("交卷成功");
            setTimeout(() => {
              this.$router.push("/space/homework");
            }, 500);
          }
        })
        .catch(err => {
          this.$toast("提交失败，请重试");
        });
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");
#cs-pra {
  width: 100%;
  min-height: 100%;
  background: #ebf0ef;
  .main {
    text-align: left;
    .content {
      width: 92vw;
      background: rgba(255, 255, 255, 1);
      padding: 4vw;
      color: #333333;
      font-size: @font-sub-title;
      margin-bottom: 2vh;
      word-break: break-all;
      .tt {
        margin-bottom: 15px;
      }
      .ct {
        text-indent: 20px;
        margin-bottom: 15px;
      }
      .ans {
        padding-left: 10px;
        .an {
          display: flex;
          margin-bottom: 10px;
        }
      }
    }

    .footer {
      display: flex;
      justify-content: space-around;
      padding: 20px 0;
      // background: #fff;
      button {
        background: rgba(0, 203, 171, 1);
        border-radius: 5px;
        font-size: @font-sub-title;
        line-height: 2;
        color: #fff;
        padding: 0 15px;
      }
    }
  }
}
</style>


